"use client";

import React from "react";
import { useRouter } from "next/navigation";
import ModuleLayout from "../../components/ModuleLayout";

const ModulePage = ({ params }: { params: { id: string } }) => {
  const router = useRouter();

  const handleExamClick = () => {
    console.log(`Examen del módulo ${params.id}`);
    // Aquí podrías redirigir al examen o manejar el desbloqueo
    router.push(`/dashboard/progress/module/${params.id}/exam`);
  };

  return (
    <div className="p-6">
      {/* Botón para volver a niveles */}
      <button
  className="text-primary-500 hover:text-primary-600 underline mb-4 flex items-center space-x-2 transition-colors duration-200 ease-in-out focus:outline-none focus:ring-2 focus:ring-primary-300"
  onClick={() => router.push("/")}
>
  <span className="text-lg font-bold">&larr;</span>
  <span className="text-sm font-medium">Volver al Inicio</span>
</button>


      {/* Contenido del módulo */}
      <ModuleLayout
        title={`Módulo ${params.id}`}
        subtitle="Subtítulo del módulo"
        videoUrl="https://www.youtube.com/embed/example"
        description="Este es un módulo sobre un tema importante del nivel."
        resources={[
          { title: "Recurso 1", link: "/dashboard/resources/1" },
          { title: "Recurso 2", link: "/dashboard/resources/2" },
        ]}
        onExamClick={handleExamClick}
      />
    </div>
  );
};

export default ModulePage;
